import React from 'react';
import {
    Panel, PanelHeader, PanelHeaderBack,
    FixedLayout, Tabs, TabsItem, Div
} from '@vkontakte/vkui';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';

import styles from "../styles/custom-styles.sass";
import ListUsers from './ListUsers';

import { Spinner } from '@vkontakte/vkui';

const Top = ({ go, id, tabGeneral, tabFriends, tabGroups,
    activeTab, userPlace, rating, goHome, groupsTop,
    globalTop, friendsTop }) => {

    const renderGlobal = globalTop.map((item, index) => {
        return (
            <ListUsers
                key={item.id}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                rating={item.rating}
                amount={item.durkaCoins}
            />
        );
    });

    const renderFriends = friendsTop.map((item, index) => {
        return (
            <ListUsers
                key={item.id}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                rating={item.rating}
                amount={item.durkaCoins}
            />
        );
    });

    const renderGroups = groupsTop.map((item, index) => {
        return (
            <ListUsers
                key={item.id}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                rating={item.rating}
                amount={item.durkaCoins}
            />
        );
    });


    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                Топ
            </PanelHeader>

            <FixedLayout vertical="top" filled={true}>
                <Tabs >
                    <TabsItem
                        onClick={tabGeneral}
                        selected={activeTab === 'general'}
                    >
                        Общий
                    </TabsItem>
                    <TabsItem
                        onClick={tabFriends}
                        selected={activeTab === 'friends'}
                    >
                        Друзья
                    </TabsItem>
                    <TabsItem
                        onClick={tabGroups}
                        selected={activeTab === 'groups'}
                    >
                        Сообщества
                    </TabsItem>
                </Tabs>
            </FixedLayout>


            {activeTab === 'general'
                ?
                <>
                    {renderGlobal
                        ?
                        <>
                            <Div className="headerTop">
                                Ваше место: {userPlace}
                                <>
                                    &#12288;{`Ваш Рейтинг:`}
                                    <Icon16Fire className="SUKA" fill="red" />
                                    {rating}
                                </>
                            </Div>

                            <Div>
                                {renderGlobal}
                            </Div>
                        </>
                        :
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                            <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                    }
                </>
                :
                ''
            }


            {
                activeTab === 'friends'
                    ?
                    <>
                        <Div className="headerTop">
                            Ваше место: {userPlace}
                            <>
                                &#12288;{`Ваш Рейтинг:`}
                                <Icon16Fire className="SUKA" fill="red" />

                                {rating}
                            </>
                        </Div>

                        {/* <Div className="stub">
                            Раздел в разработке...
                        </Div> */}

                        <Div>
                            {renderFriends}
                        </Div>



                    </>
                    :
                    ''
            }


            {
                activeTab === 'groups'
                    ?
                    <>
                        <Div className="headerTop">
                            Раздел в разработке...
                    </Div>
                        <Div>
                            {renderGroups}
                        </Div>
                    </>
                    :
                    ''
            }

        </Panel >
    );
};

export default Top;
